    this._text_62 = this.renderer.createText(this._el_61,'\n    By downloading and using the app, you agree to share your data with researchers as part of the Britain Breathing research project. \n    Only anonymous data will be collected and the data will never be linked back to you. All electronic data are stored securely at \n    The University of Manchester and are accessible only to researchers. In order to ensure our research is reproducible, we may make \n    curated versions of the dataset (parts of the data we have used for running experiments and which underpin academic papers) openly \n    available. This means that other researchers and the general public can scrutinize our results, and use the data for their own research. \n    This data will be completely anonymised, and it will not be possible to identify individuals from the data. We will log your location \n    when you submit symptom data so we can match symptoms to environmental conditions, including pollution, pollen and weather data. W\n    e will not publish specific locations (and instead use summary data at the level of the postcode), and we will not link your name with this data.\n  ',(null as any));
